const rules = {
    phoneRule: [
        { required: true, trigger: 'blur', message: '' },
        { pattern: /^1\d{10}$/, trigger: 'blur', message: '' }
      ],
      passwordRule: { required: true, trigger: 'blur', message: '' },
      organizationRule: { required: true, trigger: 'blur', message: '' },
      roleIdRule: { required: true, trigger: 'blur', message: '' },
      menuTypeRule: { required: true, trigger: 'blur', message: '' },
      parentIdRule: { required: true, trigger: 'blur', message: '' },
      realnameRule: [
        { required: true, trigger: 'blur', message: '' },
        { max: 20, message: '20', trigger: 'blur' }
      ],
      emailRule: [
        { required: false, trigger: 'blur', message: '' },
        { type: 'email', message: '', trigger: 'blur' },
        { max: 100, message: '100', trigger: 'blur' }
      ],
      orgRemarkRule: [
        { required: false, trigger: 'blur', message: '' },
        { max: 500, message: '500', trigger: 'blur' }
      ],
      roleRemarkRule: [
        { required: false, trigger: 'blur', message: '' },
        { max: 500, message: '500', trigger: 'blur' }
      ],
      menuNameRule: [
        { required: true, trigger: 'blur', message: '' },
        { max: 20, message: '20', trigger: 'blur' }
      ],
      roleNameRule: [
        { required: true, trigger: 'blur', message: '' },
        { max: 40, message: '40', trigger: 'blur' }
      ],
      orgNameRule: [
        { required: true, trigger: 'blur', message: '' },
        { max: 40, message: '40', trigger: 'blur' }
      ],
      orgCodeRule: [
        { required: true, trigger: 'blur', message: '' },
        { max: 20, message: '20', trigger: 'blur' }
      ],
      merchantContactNameRule: [
        { required: true, trigger: 'blur', message: '' },
        { max: 50, message: '50', trigger: 'blur' }
      ],
      merchantShortened: [
        { required: false, trigger: 'blur', message: '' },
        { max: 50, message: '50', trigger: 'blur' }
      ],
      merchantContactQq: [
        { required: false, trigger: 'blur', message: 'Qq' },
        { max: 50, message: '50', trigger: 'blur' }
      ],
      merchantContactWechat: [
        { required: false, trigger: 'blur', message: '' },
        { max: 100, message: '100', trigger: 'blur' }
      ],
      merchantUrl: [
        { required: false, trigger: 'blur', message: '' },
        { max: 300, message: '300', trigger: 'blur' }
      ],
      merchantContactNameRule: [
        { required: true, trigger: 'blur', message: '' },
        { max: 50, message: '50', trigger: 'blur' }
      ],
}
export default rules


